

























.copy-package {
    @apply .flex;

    .c-textbox {
        @apply .flex;

        &.c-field {
            @apply .mb-0;
        }

        .wrapper {
            @apply .flex .flex-1;

            input {
                @apply .rounded-r-none .font-mono;
            }
        }
    }

    button.c-btn {
        @apply .text-black .border .border-solid .border-grey .rounded-l-none .border-l-0;

        -webkit-box-shadow: none !important;
        box-shadow: none !important;
    }
}
